import { registerTokens } from "functions/tokens.functions";
import Message from "../models/message.model";

export const handler = async (event: any): Promise<any> => {
  try {
    console.log(event.headers.Authorization)
    const resultTokenFunction = await registerTokens(event)
    return new Message(200, 'data create success', resultTokenFunction).response()
  } catch (error: any) {
    return new Message(500, error.message, []).response()
  }
}