import { schemaToken } from "shema/card.shema";

class Token {
    constructor(
        public email: string,
        public card_number: string,
        public cvv: string,
        public expiration_year: number,
        public expiration_month: number
    ) {
        const { error } = schemaToken.validate({
            email: this.email,
            card_number: this.card_number,
            cvv: this.cvv,
            expiration_year: this.expiration_year,
            expiration_month: this.expiration_month
        });

        if (error) {
            throw new Error('Validation failed: ' + error.details.map((d: any) => d.message).join(', '));
        }
    }

    return() {
        return new Token(
            this.email,
            this.card_number,
            this.cvv,
            this.expiration_year,
            this.expiration_month
        );
    }
}

export default Token;